  import { onMounted, onUnmounted,reactive, ref, toRefs } from 'vue';
  import { useLocaler, useLocale } from 'vue-localer';
  import VideoPlayer from '@/components/element/VideoPlayer.vue'
  import { Icon } from '@iconify/vue';


  const mystery = reactive({
    tab:{
      index:0,
      switch:(index) =>{
        mystery.tab.index = index;
      }
    },
    onResize : (e) => {
      // if(window.innerWidth<640)
      // {
      //   mystery.completed = true;
      // }
    },
    start: () =>{
      window.addEventListener('resize', mystery.onResize);
    },
    stop: () =>{
      window.removeEventListener('resize', mystery.onResize);
    }

  });

  onMounted(() => {
    mystery.start();
  });

  onUnmounted(()=>{
    mystery.stop();
  });

</script>

